import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PersonalityData } from 'src/app/common/dto/personality-data';
import { Trait } from 'src/app/common/dto/trait';
import { UserProfile } from 'src/app/common/dto/user-profile';
import { PredictionService } from 'src/app/common/service/prediction/prediction.service';
import { UserService } from 'src/app/common/service/user/user.service';

@Component({
	selector: 'app-predict-character',
	templateUrl: './predict-character.component.html',
	styleUrls: ['./predict-character.component.css']
})
export class PredictCharacterComponent implements OnInit {

	public characteristics: any = {};
	public user: UserProfile = new UserProfile();

	private predictionResult: any;

	constructor(private predicationService: PredictionService,
		private userService: UserService,
		private router: Router,
		private activeRoutes: ActivatedRoute) {
		this.predictionResult = this.predicationService.getPredictionResult();

		if(this.predictionResult === "") {
			this.router.navigate(["../dashboard"], {relativeTo: this.activeRoutes});
		}

		this.preProcessData(this.predictionResult);

	}

	private preProcessData(prediction: any) {
		let big5: Map<number, any> = new Map<number, string>();
		let sortedBig5: Map<number, any>;

		prediction.personality.forEach((element: any) => {
			big5.set(this.getCloseness(element.percentile), element);
		});

		sortedBig5 = new Map<number, any>([...big5.entries()].sort());

		sortedBig5.forEach((value: any, key: number) => {
			let traits: Map<number, string> = new Map<number, any>();

			value.children.forEach((trait: any) => {
				traits.set(this.getCloseness(trait.percentile), trait);
			});

			traits = new Map<number, any>([...traits.entries()].sort());
			let termsList: Trait[] = [];

			traits.forEach((trait: any, key: number) => {
				let data: any = this.getValue(value.name, trait);
				if(data.key !== "" && data.value !== ""){
					termsList.push(data);
				}

			});

			if(value.name === "Emotional range"){
				this.characteristics["Emotional".toLowerCase()] = termsList;
			} else {
				this.characteristics[value.name.toLowerCase()] = termsList;
			}



		});
	}

	ngOnInit(): void {

	}

	private getCloseness(value: number): number {
		if (value > 0.5) {
			return 1 - value;
		} else if (value < 0.5) {
			return value;
		}
		return 0;
	}

	private getValue(big: string, trait: any): Trait {
		let type: string = "low";
		if(trait.percentile > 0.5){
			type = "high";
		}

		if(PersonalityData.dataset[big] == undefined || PersonalityData.dataset[big][trait.name] == undefined){
			return {
				key: "",
				value: "",				
			}
		}

		const data: any = {
			key: PersonalityData.dataset[big][trait.name][type].term,
			value: PersonalityData.dataset[big][trait.name][type].desc,
		}

		return data;
	}

}
