import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PredictCharacterComponent } from './component/predict-character/predict-character.component';
import { PredictHomeComponent } from './component/predict-home/predict-home.component';
import { PredictInsightComponent } from './component/predict-insight/predict-insight.component';

const routes: Routes = [
  {
    path: "",
    pathMatch: "prefix",
    children: [
      {
        path: "",
        pathMatch: "full",
        redirectTo: "dashboard"
      },
      {
        path: "dashboard",
        pathMatch: "full",
        component: PredictHomeComponent
      },
      {
        path: "insight",
        pathMatch: "full",
        component: PredictInsightComponent
      },
      {
        path: "characteristics",
        pathMatch: "full",
        component: PredictCharacterComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PredictRoutingModule { }
