import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PersonalityData } from 'src/app/common/dto/personality-data';
import { UserProfile } from 'src/app/common/dto/user-profile';
import { InsightType } from 'src/app/common/enums/insight-type.enum';
import { PredictionService } from 'src/app/common/service/prediction/prediction.service';
import { UserService } from 'src/app/common/service/user/user.service';

@Component({
	selector: 'app-predict-insight',
	templateUrl: './predict-insight.component.html',
	styleUrls: ['./predict-insight.component.css']
})
export class PredictInsightComponent implements OnInit {

	private predictionResult: any;
	private preProcessedData: any[] = [];

	public insightType: typeof InsightType = InsightType;
	public user: UserProfile = new UserProfile();
	public overview: string = '';
	public tryToSay: string[] = [];
	public tryToDo: string[] = [];
	public tryToAvoid: string[] = [];

	constructor(private predicationService: PredictionService,
		private userService: UserService,
		private router: Router,
		private activeRoutes: ActivatedRoute) {
		this.predictionResult = this.predicationService.getPredictionResult();

		if(this.predictionResult === "") {
			this.router.navigate(["../dashboard"], {relativeTo: this.activeRoutes});
		}

		this.overview = this.getOverView(this.predictionResult);
		this.preprocessDataset(this.predictionResult);
	}

	private preprocessDataset(prediction: any): void {
		let big5: Map<number, any> = new Map<number, string>();
		let sortedBig5: Map<number, any>;

		prediction.personality.forEach((element: any) => {
			big5.set(this.getCloseness(element.percentile), element);
		});

		sortedBig5 = new Map<number, any>([...big5.entries()].sort());

		sortedBig5.forEach((value: any, key: number) => {
			this.preProcessedData.push({
				name: value.name,
				action: {
					"stats" : PersonalityData.dataset[value.name]['stats']
				}
			});
		});
	}

	private getOverView(prediction: any): string {
		let traits: Map<number, string> = new Map<number, string>();
		let sortedTraits: Map<number, string>;
		let count: number = 0;
		let overview: string = "";

		prediction.personality.forEach((element: any) => {
			element.children.forEach((trait: any) => {
				let data: any = this.getValue(element.name, trait);
				if(data.name !== "" && data.desc !== ""){
					traits.set(this.getCloseness(trait.percentile), this.getValue(element.name, trait));
				}				
			});
		});

		sortedTraits = new Map<number, any>([...traits.entries()].sort());

		sortedTraits.forEach((value: any, key: number) => {			
			if(count < 5){
				overview = overview.concat(value.desc);
			}

			count++
		});

		return overview;
	}

	private getCloseness(value: number): number {
		if (value > 0.5) {
			return 1 - value;
		} else if (value < 0.5) {
			return value;
		}
		return 0;
	}

	private getValue(big: string, trait: any): any {
		let type: string = "low";
		if(trait.percentile > 0.5){
			type = "high";
		}

		if(PersonalityData.dataset[big] == undefined || PersonalityData.dataset[big][trait.name] == undefined){
			return {
				name: "",
				desc: "",				
			}
		}

		const data: any = {
			name: PersonalityData.dataset[big][trait.name][type].term,
			desc: PersonalityData.dataset[big][trait.name][type].desc,
		}

		return data;
	}

	ngOnInit(): void {

	}

	onActionSelect(type: InsightType): void {

		switch(type) {
			case InsightType.Convince:
				this.tryToSay = this.preProcessedData[0].action["stats"]["Convince Them To Hire"].text;
				this.tryToDo = this.preProcessedData[0].action["stats"]["Convince Them To Hire"].do;
				this.tryToAvoid = this.preProcessedData[0].action["stats"]["Convince Them To Hire"].donts;
				break;
			case InsightType.FollowUp:
				this.tryToSay = this.preProcessedData[0].action["stats"]["Follow Up"].text;
				this.tryToDo = this.preProcessedData[0].action["stats"]["Follow Up"].do;
				this.tryToAvoid = this.preProcessedData[0].action["stats"]["Follow Up"].donts;
				break;
			case InsightType.Impress:
				this.tryToSay = this.preProcessedData[0].action["stats"]["Make A Good Impression"].text;
				this.tryToDo = this.preProcessedData[0].action["stats"]["Make A Good Impression"].do;
				this.tryToAvoid = this.preProcessedData[0].action["stats"]["Make A Good Impression"].donts;
				break;
			case InsightType.Negotiate:
				this.tryToSay = this.preProcessedData[0].action["stats"]["Negotiate"].text;
				this.tryToDo = this.preProcessedData[0].action["stats"]["Negotiate"].do;
				this.tryToAvoid = this.preProcessedData[0].action["stats"]["Negotiate"].donts;
				break;
			case InsightType.Pricing:
				this.tryToSay = this.preProcessedData[0].action["stats"]["Discuss Pricing"].text;
				this.tryToDo = this.preProcessedData[0].action["stats"]["Discuss Pricing"].do;
				this.tryToAvoid = this.preProcessedData[0].action["stats"]["Discuss Pricing"].donts;
				break;
			case InsightType.Sales:
				this.tryToSay = this.preProcessedData[0].action["stats"]["Make A Sales Pitch"].text;
				this.tryToDo = this.preProcessedData[0].action["stats"]["Make A Sales Pitch"].do;
				this.tryToAvoid = this.preProcessedData[0].action["stats"]["Make A Sales Pitch"].donts;
				break;
		}
	}

}
