import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-predict-insight-nav',
  templateUrl: './predict-insight-nav.component.html',
  styleUrls: ['./predict-insight-nav.component.css']
})
export class PredictInsightNavComponent implements OnInit {

  constructor(private router: Router) {
  }

  ngOnInit(): void {
  }

  navigateToHome(): void {
    this.router.navigateByUrl("/home")
  }

  navigateToCharacter(): void {
    this.router.navigateByUrl("/predict/characteristics")
  }

}
