import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { LaunchActivity } from 'src/app/common/interface/launch-activity';
import { ActivityService } from 'src/app/common/service/activity/activity.service';
import { PredictionService } from 'src/app/common/service/prediction/prediction.service';

@Component({
	selector: 'app-predict-home',
	templateUrl: './predict-home.component.html',
	styleUrls: ['./predict-home.component.css']
})
export class PredictHomeComponent implements OnInit {

	public predictForm: FormGroup;

	constructor(private router: Router, private activeRoute: ActivatedRoute, 
		private formBuilder: FormBuilder, private toastr: ToastrService,
		private predictionService: PredictionService) {
		this.predictForm = this.formBuilder.group({
			'url': ['', [Validators.required, Validators.pattern(/^(https:\/\/www\.linkedin\.com\/in\/).*(\/)/gm)]]
		})
	}

	ngOnInit(): void {
	}

	navigateToInsight(): void {
		this.router.navigate(['../insight'], { relativeTo: this.activeRoute });
	}

	onSubmit(): void {
		this.predictForm.markAllAsTouched();
		if(this.predictForm.valid) {
			this.predictionService.predictByUrl(this.predictForm.get("url")?.value).then(
				(status: boolean) => {
					if(status) {
						this.navigateToInsight();
					}
				}
			)
		} else {
			this.toastr.error("Please provide valid user profile url.");
		}
	}

}
