import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { CampaignUrlType } from '../../enums/campaign-url-type.enum';
import { RequestType } from '../../enums/request-type.enum';
import { RequestFactory } from '../../factory/request/request-factory';
import { UuidUtils } from '../../utils/uuid-utils';
import { TabService } from '../tab/tab.service';
import { TokenService } from '../token/token.service';
import { UrlUtils } from '../../utils/url-utils';
import { MessageService } from '../message/message.service';
import { EventType } from '../../enums/event-type.enum';
import { IPayload } from '../../interface/ipayload';
import { PersonParserUtils } from '../../utils/person-parser-utils';
import { ToastrService } from 'ngx-toastr';
import { HttpClient } from '@angular/common/http';
import { PredictionServer } from '../../dto/prediction-server';

@Injectable({
	providedIn: 'root'
})
export class PredictionService {

	private predictionResult: any;

	constructor(private factory: RequestFactory,
		private tokenService: TokenService,
		private tabService: TabService,
		private messageService: MessageService,
		private toaster: ToastrService) {
		this.predictionResult = "";
	}

	getPredictionResult(): any {
		return this.predictionResult;
	}

	predictByUrl(url: string): Promise<boolean> {
		return new Promise<boolean>(async (resolve, reject) => {

			this.toaster.info("Please allow us 50-60 sec. to load information about the user.");

			const requestType: CampaignUrlType = UrlUtils.getUrlType(url);
			const uuid: string = UuidUtils.getUUID();
			await this.tabService.validateLinkedInTabStatus(requestType, uuid);

			const tabId: number | undefined = await this.tabService.getTabIdBasedOnCampaignType(requestType, uuid);
			const token: string = this.tokenService.getToken();

			if (tabId === undefined || !this.tokenService.isTokenAvaliable()) {
				await this.tabService.destroyCreatedTabs(uuid);
				return reject({
					message: "Could not process the request due to 'Linkedin service is unavalible Please logout from LinkedIn and Syncup AI close the browser and try again.'"
				})
			}
			
			const fullDataUrl: string = UrlUtils.getPredictionUrl(environment.api.prediction.request.linkedin, url);
			
			const predictionRequest: IPayload = this.factory.getPayload(RequestType.Prediction, fullDataUrl, token) as unknown as IPayload;
			
			this.messageService.sendMessageToTab(tabId, EventType.PREDICTION, predictionRequest, this.processResult(uuid, resolve, reject));
		});
	}

	private processResult(uuid: string, resolve: (status: boolean)=> void, reject: (status: boolean)=> void): ((result: any) => void) {
		return (result: any) : void  => {
			if (result != undefined) {
				const text: string = PersonParserUtils.convertToText(result);
				if(text.length < 400) {
					this.toaster.error("User has not provided enough information about himself/herself to make a prediction.");
					return reject(false);
				}
				
				if(environment.api.prediction.closeTab){
					this.tabService.destroyCreatedTabs(uuid);
				}
				this.processServerRequest(text, resolve, reject);

			} else {
				this.toaster.error("Could not process the request due to 'Linkedin service is unavalible Please logout from LinkedIn and Syncup AI close the browser and try again.")
				return reject(false);
			}
		}
	}

	private processServerRequest(text: string, resolve: (status: boolean) => void, reject: (status: boolean) => void) {
		const url: string = environment.api.prediction.request.server;
		const token: string = environment.api.prediction.request.token;
		const payload: PredictionServer = this.factory.getPayload(RequestType.PredictionServer, url, token) as unknown as PredictionServer;
		payload.service.body = text;

		fetch(payload.url, payload.service).then(
			async (result: any) => {
				this.predictionResult = await result.json();
				return resolve(true);
			},
			(error: any) => {
				this.toaster.error("Could not process the request due to 'Prediction server is offline'.");
				return reject(false);
			}			
		)
		
	}
	
}
