import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { InsightType } from 'src/app/common/enums/insight-type.enum';

@Component({
  selector: 'app-predict-insight-btn',
  templateUrl: './predict-insight-btn.component.html',
  styleUrls: ['./predict-insight-btn.component.css']
})
export class PredictInsightBtnComponent implements OnInit {

  @Input("text")
  public text: string = "";

  @Input("type")
  public type: InsightType = InsightType.None;

  @Output("onActionSelect")
  public onActionSelectEvent: EventEmitter<InsightType>;

  constructor() {
    this.onActionSelectEvent = new EventEmitter<InsightType>();
  }

  ngOnInit(): void {
  }

  onActionSelect(): void {
    this.onActionSelectEvent.emit(this.type);
  }

}
